* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px;
}

input{
    color: grey;
    font-size: 1.3rem;
    width: 100%;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    text-align: center;
    padding: 5px;
}

img {
    max-height: 150px;
}
